// src/components/SummaryCard.js
import React from "react";

export default function SummaryCard({ transactions }) {
  const total = transactions.reduce((sum, t) => sum + t.amount, 0);
  const count = transactions.length;
  const avg = count > 0 ? (total / count).toFixed(2) : 0;

  return (
    <div className="bg-blue-50 p-4 rounded-2xl shadow text-blue-900">
      <h2 className="text-xl font-semibold mb-2"> Summary</h2>
      <div className="grid grid-cols-3 gap-4 text-center">
        <div>
          <p className="text-lg font-medium">Total Spent</p>
          <p className="text-2xl font-bold">{total.toFixed(2)}</p>
        </div>
        <div>
          <p className="text-lg font-medium">Transactions</p>
          <p className="text-2xl font-bold">{count}</p>
        </div>
        <div>
          <p className="text-lg font-medium">Avg per Transaction</p>
          <p className="text-2xl font-bold">{avg}</p>
        </div>
      </div>
    </div>
  );
}