import React, { useState } from "react";

import CsvUpload from "./components/CsvUpload";
import SummaryCard from "./components/SummaryCard";
import ExpensePieChart from "./components/ExpensePieChart";
import CategoryBreakdown from "./components/CategoryBreakdown";
import MonthlySummaryChart from "./components/MonthlySummaryChart";
import TransactionTable from "./components/TransactionTable";
import AISuggestions from "./components/AISuggestions";

/* 
   Simple rule-based classifier
    */
const classify = (desc = "") => {
  const d = desc.toLowerCase();
  const necessary   = ["rent", "bill", "grocery", "medicine", "electric", "fee"];
  const unnecessary = ["coffee", "movie", "netflix", "uber", "shopping", "snack"];

  if (necessary.some((w) => d.includes(w)))   return "Necessary";
  if (unnecessary.some((w) => d.includes(w))) return "Unnecessary";
  return "Necessary";
};

export default function App() {
  const [transactions, setTransactions] = useState([]);

  /* called when CsvUpload finishes parsing */
  const handleCsvData = (rows) => {
    const parsed = rows.map((r) => ({
      date:        r.Date        || r.date        || "",
      description: r.Description || r.description || "",
      category:    r.Category    || r.category    || "Other",
      amount:      parseFloat(r.Amount || r.amount || 0),
      type:        classify(r.Description || r.description || ""),
    }));
    setTransactions(parsed);
  };

  /* quick totals for tiles & pie */
  const necessaryTotal = transactions
    .filter((t) => t.type === "Necessary")
    .reduce((sum, t) => sum + t.amount, 0);

  const unnecessaryTotal = transactions
    .filter((t) => t.type === "Unnecessary")
    .reduce((sum, t) => sum + t.amount, 0);

  return (
    <div className="min-h-screen bg-gray-100 p-6">
      <div className="max-w-5xl mx-auto space-y-6">

        {/* CSV upload */}
        <div className="bg-white rounded-2xl shadow-md p-6">
          <CsvUpload onDataParsed={handleCsvData} />
        </div>

        {/* grand summary */}
        <SummaryCard transactions={transactions} />

        {/* necessary / unnecessary tiles */}
        <div className="grid grid-cols-2 gap-4">
          <div className="bg-green-100 p-4 rounded-2xl shadow">
            <h3 className="text-lg font-medium text-green-800">Necessary</h3>
            <p className="text-2xl font-bold text-green-900">
              {necessaryTotal.toFixed(2)}
            </p>
          </div>
          <div className="bg-red-100 p-4 rounded-2xl shadow">
            <h3 className="text-lg font-medium text-red-800">Unnecessary</h3>
            <p className="text-2xl font-bold text-red-900">
              {unnecessaryTotal.toFixed(2)}
            </p>
          </div>
        </div>

        {/* pie chart */}
        <div className="bg-white rounded-2xl shadow-md p-6">
          <h2 className="text-xl font-semibold text-center mb-4">
            Spending Pie
          </h2>
          <ExpensePieChart transactions={transactions} />
        </div>

        {/* category bar chart */}
        <CategoryBreakdown transactions={transactions} />

        {/* AI suggestions */}
        <AISuggestions transactions={transactions} />

        {/* monthly trend (fetches its own data) */}
        <div className="bg-white rounded-2xl shadow-md p-6">
          <h2 className="text-xl font-semibold text-center mb-4">
            Monthly Trend
          </h2>
          <MonthlySummaryChart />
        </div>

        {/* table */}
        <div className="bg-white rounded-2xl shadow-md p-6">
          <TransactionTable transactions={transactions} />
        </div>
      </div>
    </div>
  );
}