import React from "react";
import {
  BarChart,
  Bar,
  XAxis,
  YAxis,
  Tooltip,
  ResponsiveContainer,
  Cell,
} from "recharts";

/* nice rotating colour palette */
const COLORS = [
  "#00C49F",
  "#FFBB28",
  "#0088FE",
  "#FF8042",
  "#845EC2",
  "#D65DB1",
];

export default function CategoryBreakdown({ transactions = [] }) {
  /* guard-rail: render nothing until we actually have rows */
  if (transactions.length === 0) return null;

  /* group totals by category (fallback  Other) */
  const totals = {};
  transactions.forEach((t) => {
    const cat = (t.category || "Other").trim();
    totals[cat] = (totals[cat] || 0) + t.amount;
  });

  const data = Object.entries(totals).map(([name, value]) => ({
    name,
    value,
  }));

  return (
    <div className="bg-white rounded-2xl shadow-md p-6">
      <h2 className="text-xl font-semibold text-center mb-4">By Category</h2>

      <ResponsiveContainer width="100%" height={250}>
        <BarChart data={data} layout="vertical">
          <XAxis type="number" hide />
          <YAxis type="category" dataKey="name" width={100} />
          <Tooltip formatter={(v) => `${v.toFixed(2)}`} />
          <Bar dataKey="value">
            {data.map((_, idx) => (
              <Cell key={idx} fill={COLORS[idx % COLORS.length]} />
            ))}
          </Bar>
        </BarChart>
      </ResponsiveContainer>
    </div>
  );
}