import React, { useEffect, useState } from "react";
import axios from "axios";

export default function AISuggestions({ transactions }) {
  const [insights, setInsights] = useState(null);

  useEffect(() => {
    if (transactions.length) {
      axios.post("http://localhost:8080/api/ai/suggestions", transactions)
        .then((res) => setInsights(res.data))
        .catch(console.error);
    }
  }, [transactions]);

  if (!insights) return null;

  return (
    <div className="bg-white rounded-2xl shadow-md p-6 mt-6">
      <h2 className="text-xl font-semibold text-center mb-4"> AI Suggestions</h2>
      <p><strong>Total Spending:</strong> {insights.totalSpending.toFixed(2)}</p>
      <p><strong>Unnecessary Spending:</strong> {insights.unnecessarySpending.toFixed(2)}</p>
      <p><strong>Savings Potential:</strong> {insights.savingsPotential.toFixed(2)}</p>

      <h3 className="font-medium mt-4 mb-2">Suggestions:</h3>
      <ul className="list-disc ml-5">
        {insights.tips.map((tip, i) => (
          <li key={i}>{tip}</li>
        ))}
      </ul>
    </div>
  );
}